@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Montserrat', sans-serif;
  background-color: hsl(218, 29%, 95%);

}

.App{background-color:#eff1f6;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:80%;padding:0;margin:0 auto}.questions{padding:2em}.title,.question-type{color:black}.question-link{color:black;text-decoration:none;margin:0;margin:1em}

.QuestionPage{display:-webkit-flex;display:flex;height:100vh;width:100%;margin:0;padding:0}.video-container{padding:1em;height:600px;width:600px}button:active{background-color:yellow}@media screen and (max-width: 768px){.QuestionPage{-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.video-container{width:70%}}

h1{font-size:3em}.hero-row{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin:0 auto}.hero-keyword{color:#0d61f2}.hero-row-left{padding:1em}.hero-image{width:50%;margin-left:auto}

